.gpt3__whatgpt3 {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background: var( --color-footer);

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 4% 4%, rgba(15, 66, 121, 1) 0%, rgba(4, 44, 84, 1) 47%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 4% 4%, rgba(15, 66, 121, 1) 0%, rgba(4, 44, 84, 1) 47%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 4% 4%, rgba(15, 66, 121, 1) 0%, rgba(4, 44, 84, 1) 47%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 4% 4%, rgba(15, 66, 121, 1) 0%, rgba(4, 44, 84, 1) 47%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 4% 4%, rgba(15, 66, 121, 1) 0%, rgba(4, 44, 84, 1) 47%, rgba(27, 120, 222, 1) 100%);

}

.gpt3__whatgpt3-feature {
    display: flex;
}

.gpt3__whatgpt3-feature .gpt3__whatgpt3-features-container__feature {
    margin: 0;

}

.gpt3__whatgpt3-feature .gpt3__whatgpt3-features-container__feature-text {
    max-width: 700px;
}

.gpt3__whatgpt3-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 1rem 2rem;
}

.gpt3__whatgpt3-heading h1 {
    font-family: var( --font-family);
    font-weight: 800;
    max-width: 510px;
    font-size: 34px;
    line-height: 45px;
}

.gpt3__whatgpt3-heading p {
    font-family: var( --font-family);;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var( --color-subtext);
    cursor: pointer;
}

.gpt3__whatgpt3-heading p:hover {
    color: var(--color-white);
}

.gpt3__whatgpt3-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}